import { isObject, isString, isFunction } from '../../utils';

let id = 0;

export default {
	data() {
		return {
			list: []
		};
	},

	methods: {
		open(d) {
			let options = {
				id: id++,
				visible: false,
				closed: false,
				icon: '',
				message: '',
				duration: 2000,
				type: 'default',
				position: 'bottom',
				onClose: null,
				iconSize: 22
			};

			if (isObject(d)) {
				Object.assign(options, d);
			} else {
				options.message = d;
			}

			this.list.push(options);

			setTimeout(() => {
				this.create(options);
			}, 50);
		},

		close(item) {
			item.visible = false;
			item.closed = true;

			if (isFunction(item.onClose)) {
				item.onClose(this);
			}
		},

		create(item) {
			const { duration } = item || {};

			if (duration > 0) {
				item.visible = true;
				setTimeout(() => {
					this.close(item);
				}, duration);
			}
		}
	}
};
